import { useEffect } from 'react';
import { debounce } from '@yomua/y-screw';

const DEFAULT_OPTIONS = {
    delay: 0,
};
//  hook 
function useWindowEventListener(eventName, listenCallback, effect, options = DEFAULT_OPTIONS) {
    if (!eventName) {
        return;
    }
    const { delay = 0, isRefreshItself = false } = options;
    useEffect(() => {
        window.addEventListener(eventName, debounce(listenCallback, delay, { isRefreshItself }));
        return () => {
            window.removeEventListener(eventName, debounce(listenCallback, delay, { isRefreshItself }));
        };
    }, effect);
}

export { useWindowEventListener as default };
